.header-policy
    width: 100%
    position: absolute
    padding: 45px 0 20px
    z-index: 5
    background-color: $blue-dark-color
    @include xs
        padding-top: 40px
    @include xxs
        padding-top: 35px
    .container
        @include sm 
            display: flex
            justify-content: center
    &__logo
        display: inline-block
        width: 160px
        @include lg
            width: 150px
        @include md
            width: 140px
        @include sm
            width: 130px
        @include xs
            width: 120px
        @include xxs
            width: 110px
        @include xxxs
            width: 100px
