.policy
  width: 100%
  position: relative
  margin: 0 auto 20px
  padding-top: 136px
  @include sm 
    padding-top: 124px
  @include xs
    padding-top: 114px
    margin: 0 auto 10px
  &__title-h1
    font-size: 40px
    font-family: $bold
    font-weight: 700
    line-height: 1.2
    margin-bottom: 50px
    color: $main-color
    @include sm
      font-size: 36px
      margin-bottom: 30px
    @include xs
      font-size: 30px
      margin-bottom: 25px
  &__title-h3
    font-size: 20px
    font-family: $bold
    font-weight: 700
    line-height: 1.2
    margin-bottom: 15px
    color: $main-color
    @include sm
      font-size: 18px
      margin-bottom: 12px
    @include xs
      font-size: 16px
      margin-bottom: 10px
  &__text
    max-width: 875px
    font-size: 16px
    font-family: $reg
    font-weight: 400
    line-height: 1.5
    margin-bottom: 18px
    color: $main-color
    @include sm
      margin-bottom: 14px
    @include xs
      font-size: 14px
      margin-bottom: 10px
  &__text-link
    color: $main-color
    transition: all .3s 
    &:hover
      color: $btn-bg-cookie-color

.section
  margin-bottom: 50px
  @include sm
      margin-bottom: 30px
  @include xs
      margin-bottom: 25px
